import { combineReducers } from 'redux'

import {
    LOAD_NEWS,
    LOAD_NEWS_SUCCESS,
    LOAD_NEWS_FAILURE
} from '../actions'

import { STATUS_DONE, STATUS_ERROR, STATUS_LOAD } from '../constants'

export const elements = (state=[], action) => {
    switch (action.type) {
        case LOAD_NEWS_SUCCESS:
            return action.news
        default:
            return state
    }
}

export const selectedCount = (state=0, action) => {
    switch (action.type) {
        case LOAD_NEWS_SUCCESS:
            return 0
        default:
            return state
    }
}

const elementCount = (state = null, action) => {
    switch (action.type) {
        case LOAD_NEWS_SUCCESS:
            console.log(action.news.length)
            return action.news.length
        default:
            return state
    }
}

const fetchStatus = (state = STATUS_LOAD, action) => {
    switch (action.type) {
        case LOAD_NEWS: 
            return STATUS_LOAD
        case LOAD_NEWS_SUCCESS:
            return STATUS_DONE
        case LOAD_NEWS_FAILURE:
            return STATUS_ERROR
        default:
            return state
    }
}

export default combineReducers({
    selectedCount,
    elements,
    elementCount,
    fetchStatus
})


